import React from "react";
import { Link, useSearchParams } from "@remix-run/react";

/**
 * @type {React.FC<{
 * totalPages: number|string
 * pageParam?: string
 * }>}
 */
const Pagination = ({
  totalPages = Number.MAX_SAFE_INTEGER,
  pageParam = "page",
  className = "",
  ...attrs
}) => {
  const [queryParams] = useSearchParams();
  const currentPage = Number(queryParams.get(pageParam) || 1);
  totalPages = Number(totalPages);

  const previousQuery = new URLSearchParams(queryParams);
  previousQuery.set(pageParam, currentPage - 1);
  const nextQuery = new URLSearchParams(queryParams);
  nextQuery.set(pageParam, currentPage + 1);

  return (
    <nav
      id="main-nav"
      style={{
        display: "flex",
        justifyContent: "center",
        padding: "10px",
        margin: "10px",
      }}
      className={["flex justify-between", className].filter(Boolean).join(" ")}
      {...attrs}>
      <div style={{ display: "flex", margin: "5px", marginRight: "10vw" }}>
        {currentPage <= 1 && <span>Previous Page</span>}
        {currentPage > 1 && (
          <Link to={`?${previousQuery.toString()}`}>Previous Page</Link>
        )}
      </div>
      <div
        style={{
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          columnGap: "5rem",
        }}>
        <div>
          {currentPage <= 1 && <span>First page</span>}
          {currentPage > 1 && (
            <Link to={`?${previousQuery.toString()}`}>{currentPage - 1}</Link>
          )}
        </div>
        <div>{currentPage}</div>
        <div>
          {currentPage >= totalPages && <span>Last page</span>}
          {currentPage < totalPages && (
            <Link to={`?${nextQuery.toString()}`}>{currentPage + 1}</Link>
          )}
        </div>
      </div>

      <div style={{ display: "flex", margin: "5px", marginLeft: "10vw" }}>
        {currentPage >= totalPages && <span>Next Page</span>}
        {currentPage < totalPages && (
          <Link to={`?${nextQuery.toString()}`}>Next Page</Link>
        )}
      </div>
    </nav>
  );
};

export default Pagination;
